header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

/* SOCIALS */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials a:hover {
    transform: scale(1.5);
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-light);
}

/* IMAGE */

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 21rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 1rem 0.5rem 0.5rem 0.5rem;
}

.me > img {
    border-radius: 12rem 12rem 0 0;
}

.logo__container {
    width: 13rem;
    height: auto;
    position: absolute;
    left: calc(50% - 6.5rem);
    top: 0;
    margin-top: -5rem;
    top: 0;
}

.logo__container > img {
    border-radius: 12rem;
}

.cta__container {
    width: 18rem;
    height: auto;
    position: absolute;
    left: calc(50% - 9rem);
    top: 0;
    margin-top: 8rem;
    top: 0;
}


/* Scroll Down */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

.avatar {
    position: absolute;
    left: calc(50% - 4rem);
    margin-top: 30rem;
}

.avatar > img {
    width: 8rem;
    height: 8rem;
    border-radius: 12rem;
}

.avatar > p {
    color: var(--color-light)
}


/* media queries (tablets) */ 

@media screen and (max-width: 1024px) {
    header {
        height: 100vh;
    }
}

/* media queries (phones) */ 

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }
    .header__socials, .scroll__down {
        display: none;
    }


}